html,
body {
  height: 100%;
  margin: 0;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
}

#quote-box {
  text-align: center;
}

#text {
  font-size: 150%;
}

.poster-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 250px;
  height: 350px;
}

.controls-container {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  font-size: 110%;
  padding: 10px;
  margin: 10px;
}
